import ConnectPlugin from "@/Components/connectPlugins";
import { GetAddonsList } from "@/utilities/cookies";
import { notFound } from "next/navigation";
import createMetadata from "@/Components/Seo/createMetadata";

export async function generateMetadata() {
  const title = "Review";

  return createMetadata({
    title: title,
  });
}

export default async function review(props) {
  const params = await props.params;
  const addonList = await GetAddonsList();
  if (addonList && addonList?.["rating-review"]?.status == 0) {
    notFound();
  }

  if (!addonList?.["rating-review"]) {
    notFound();
  }

  return (
    addonList &&
    addonList?.["rating-review"]?.status == 1 &&
    ConnectPlugin.OrderDetailAddReview && (
      <ConnectPlugin.OrderDetailAddReview orderId={params?.id} />
    )
  );
}
